.leader-sells {
    padding: 96px 0;
}
.item {
    display: flex;
    width: 25%;
    margin-top: 30px;
    text-align: center;
    &__inner {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        padding: 130px 30px 35px;
        background-color: var(--itemBg);
        border-radius: var(--borderRadius);
        width: 100%;
        .title-h3, .title-h1 {
            margin-top: 22px;
            display: block;
            &::after {
                content: "";
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                z-index: 1;
            }
            span {
                font-weight: 400;
                display: block;
            }
        }
        &:hover {
            img {
                transform: scale(1.2);
            }
            .title-h3 {
                opacity: 0;
            }
            .item__price {
                visibility: hidden;
            }
        }
    }
    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 320px;
        width: 100%;
        margin: 0 auto;
        img {
            max-width: 100%;
            max-height: 100%;
            transition: .5s;
        }
    }
    &__price {
        white-space: nowrap;
        margin: 25px 0 0;
    }
    &__tag, &__tag-light {
        position: absolute;
        border-radius: var(--borderRadius);
        text-align: center;
        text-transform: uppercase;
    }
    &__tag {
        top: 30px;
        left: 30px;
        color: var(--White);
        width: 100px;
        background-color: var(--mainColor);
        padding: 16.2px 10px;
        &-light {
            top: 50px;
            left: 50px;
            background-color: var(--darkWhite);
            padding: 17px 30px;
            color: var(--tagColor);
            font-size: 16px;
            z-index: 1;
        }
    }
    &__action {
        position: absolute;
        top: 30px;
        right: 30px;
        z-index: 1;
        &-btn {
            display: block;
            position: relative;
            z-index: 1;
            input {
                display: none;
            }
            input + label {
                width: 50px;
                height: 50px;
                border-radius: var(--borderRadius);
                background-color: var(--White);
                cursor: pointer;
            }
            i {
                font-size: 20px;
            }
            .icon-1-2_heart {
                font-size: 18px;
            }
            &:not(:last-child) {
                margin-bottom: 10px;
            }
            &.heart {
                input:checked + label, &:hover {
                    color: var(--Red);
                }
            }
            &.basket {
                input:checked + label, &:hover {
                    color: var(--Grey);
                }
            }
        }
    }
    &__left {
        width: 200px;
    }
    &__right {
        margin-left: 15px;
    }
}
@media(max-width: 1600px) {
    .item {
        &__inner {
            padding: 120px 20px 25px;
        }
        &__action {
            top: 20px;
            right: 20px;
        }
        &__tag {
            top: 20px;
            left: 20px;
        }
    }
}
@media(max-width: 1450px) {
    .item {
        width: 33.3%;
    }
}
@media(max-width: 1200px) {
    .item {
        width: 50%;
    }
}
@media(max-width: 900px) {
    .leader-sells {
        padding: 60px 0;
    }
}
@media(max-width: 767px) {
    .item {
        width: 100%;
    }
}